.postbox.sc-heading div {
  font-weight: normal;
  padding: 10px 15px 10px 15px;
  font-size: 24px;
}


.sc-postbox {
  padding: 30px 15px;

  h4 {
    font-size: 22px;
    margin: 0em 0 1em 0;
  }

  .label-field-wrapper {

    position: relative;
    height: 30px;

    &:before {
        content: '';
        height: 100%;
        display: inline-block;
        vertical-align: middle;
    }

    margin-bottom: 20px;
    min-height: 30px;

    label {
      display: inline-block;
      width: 300px;
      vertical-align: middle;
      font-size: 15px;
    }


    .sc-option-field {
      display: inline-block;
    }
    
  }

}
